	ketToStan () {
		for (var i = 0; i < 16; i++)
		{
			for (var j = 1; j < 11; j++)
			{
				if ((this.state.ketStansTable[i][j] >= 0) && (this.state.old_ketResults[i] <= this.state.ketStansTable[i][j]) && (this.state.old_ketResults[i] > this.state.ketStansTable[i][j-1]))
				{
					this.state.ketStans[i] = j;
				}
			}
		}
	}

	ketFactoring () {
		for (var i = 0; i < 16; i++)
		{
			if (this.state.ketStans[i] <= 3 && this.state.ketStans[i] >= 1)
			{
				this.state.ketFactors[i] = this.state.ketMarks[i][0];
			}
			else if (this.state.ketStans[i] <= 5 && this.state.ketStans[i] >= 4)
			{
				this.state.ketFactors[i] = this.state.ketMarks[i][1];
			}
			else if (this.state.ketStans[i] <= 7 && this.state.ketStans[i] >= 6)
			{
				this.state.ketFactors[i] = this.state.ketMarks[i][2];
			}
			else if (this.state.ketStans[i] <= 10 && this.state.ketStans[i] >= 8)
			{
				this.state.ketFactors[i] = this.state.ketMarks[i][3];
			}
		}
	}

