	schwToSequence () {
		// 1 
		var sequence1 = [[this.state.schwRang1[0], 0], [this.state.schwRang1[1], 1], [this.state.schwRang1[2], 2], [this.state.schwRang1[3], 3], [this.state.schwRang1[4], 4],
						 [this.state.schwRang1[5], 5], [this.state.schwRang1[6], 6], [this.state.schwRang1[7], 7], [this.state.schwRang1[8], 8], [this.state.schwRang1[9], 9]];

		for (var i = 0; i < 10; i++)
			for (var j = 0; j < 9; j++)
				if (sequence1[j][0] > sequence1[j + 1][0]) {
					var temp = sequence1[j];
					sequence1[j] = sequence1[j + 1];
					sequence1[j + 1] = temp;
				}

		this.setState({ schwSequence1: [sequence1[0][1], sequence1[1][1], sequence1[2][1], sequence1[3][1], sequence1[4][1],
										sequence1[5][1], sequence1[6][1], sequence1[7][1], sequence1[8][1], sequence1[9][1]] });

		// 2 
		var sequence2 = [[this.state.schwRang2[0], 0], [this.state.schwRang2[1], 1], [this.state.schwRang2[2], 2], [this.state.schwRang2[3], 3], [this.state.schwRang2[4], 4],
						 [this.state.schwRang2[5], 5], [this.state.schwRang2[6], 6], [this.state.schwRang2[7], 7], [this.state.schwRang2[8], 8], [this.state.schwRang2[9], 9]];

		for (var i = 0; i < 10; i++)
			for (var j = 0; j < 9; j++)
				if (sequence2[j][0] > sequence2[j + 1][0]) {
					var temp = sequence2[j];
					sequence2[j] = sequence2[j + 1];
					sequence2[j + 1] = temp;
				}

		this.setState({ schwSequence2: [sequence2[0][1], sequence2[1][1], sequence2[2][1], sequence2[3][1], sequence2[4][1],
										sequence2[5][1], sequence2[6][1], sequence2[7][1], sequence2[8][1], sequence2[9][1]] });
	}

